@media screen and (max-width: 1199px) {
  .logo {
    background-size: 25px;
    padding-left: 31px;
  }
  .compass {
    font-size: 16px;
    letter-spacing: 3px;
  }

  .menu__link {
    font-size: 11px;

    &:hover {
      font-size: 13px;
    }
  }
  .phone__tel {
    font-size: 14px;
  }
  .first-title {
    letter-spacing: 40px;
  }
  .question .third-title {
    text-align: center;
  }
  .question {
    padding: calc(100vh / 20) 78px;
  }
  .its-time {
    text-align: center;
  }
  .content-box {
    padding: 40px 37px 60px;
  }

  .contacts__inner {
    width: 100%;
    justify-content: center;
  }
  .contacts__inner-right {
    padding: 20px;
    text-align: center;
  }
  .first-title_small {
    letter-spacing: 16px;
    font-size: 50px;
  }
}

@media screen and (max-width: 991px) {
  .first-title {
    font-size: 71px;
    letter-spacing: 40px;
  }
  .europe {
    right: 29px;
  }
  .main__p {
    font-size: 46px;
    letter-spacing: 10px;
  }
  .advantages__wrap {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 1fr 1fr;
  }
  .advantages__item_1 {
    grid-column: 1 / 4;
  }

  .advantages__item_2 {
    grid-column: 4 / 7;
  }

  .advantages__item_3 {
    grid-column: 2 / 6;
  }
  .owl-prev {
    left: -20px;
  }
  .owl-next {
    right: -20px;
    left: auto;
  }
  .property__text-wrap {
    padding: 56px 15px 36px;
  }
  .contacts__inner-wrap {
    flex-direction: column;
  }
  .first-title_small {
    letter-spacing: 16px;
    font-size: 50px;
  }
  .content-box-about-text-1 .text-accent {
    font-size: 35px;
  }
  .content-box-about-text-1 {
    font-size: 13px;
  }
  .content-box-about-text-2 {
    letter-spacing: 5px;
  }
  .menu-wrapper{
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .first-title {
    font-size: 70px;
    letter-spacing: 20px;
  }
  .second-title {
    font-size: 36px;
    margin-bottom: 54px;
    text-align: center;
  }
  .advantages__item {
    grid-column: span 6;
  }
  .moving .row {
    height: auto;

    &:nth-child(2) {
      margin: 40px auto;
    }
  }
  .property__ask {
    margin-top: 67px;
  }
  .owl-item {
    padding: 0;
  }
  .feedback {
    &__item {
      padding: 140px 30px 30px 30px;
    }

    &__img-wrap {
      left: 50%;
      top: 68px;
    }

    &__row {
      flex-direction: column;
      margin: 5px;
    }

    &__link {
      margin: 10px auto;
    }

    &__text {
      text-align: center;
    }
  }
  #js-prev, #js-next {
    display: none;
  }
  .policy {
    display: none;
  }
  .footer__text-wrap {
    justify-content: flex-end;
  }
  .moving {
    height: auto;
  }
  .question {
    padding: calc(100vh / 20) 10px;
  }
  .relocation__content-wrap{
    display: block;
  }
  .relocation__content{
    width: 100%;
  }
  .relocation__content-right{
    flex-direction: column;
  }
  .relocation__content-left img,
  .relocation__content-right img {
    margin-bottom: 30px;
    margin-top: 20px;
  }
  .first-title_small {
    letter-spacing: 14px;
    font-size: 50px;
  }
  .relocation .first-title_small {
    letter-spacing: 5px;
    font-size: 50px;
  }
  .relocation__text{
    text-align: justify;
  }
  .content-box-about-text-3 {
    font-size: 20px;
    letter-spacing: normal;
  }
  .content-box-about-text-1 .text-accent {
    font-size: 27px;
  }
  .content-box-about-text-1 {
    font-size: 11px;
  }
  .partners{
    flex-direction: column;
  }
  .partner{
    width: 100%;
    margin: 10px 0;
  }
}

@media screen and (max-width: 575px) {
  .privacy {
    font-size: 11px;
  }
  .europe {
    right: 28%;
  }
  .first-title {
    font-size: 45px;
    letter-spacing: 4px;
  }
  .main__p {
    font-size: 30px;
    letter-spacing: 3px;
  }
  .second-title,
  .moving .second-title {
    font-size: 24px;
    margin-bottom: 28px;
  }
  .moving .third-title {
    font-size: 18px;
  }
  .submit-btn {
    font-size: 13px;
  }
  .phone__tel {
    display: none;
  }
  .footer__text-wrap {
    margin-top: 15px;
    justify-content: center;
  }
  .quote {
    width: 85%;
  }
  .contacts .feedback-form {
    width: 100%;
  }
  .content-box {
    padding: 30px 20px;
  }
  .contacts__body-text {
    padding: 0 10px;
  }
  .contacts__inner-left {
    padding: 15px;
  }
  .contacts__item-text {
    font-size: 14px;
  }
  .relocation .first-title_small {
    letter-spacing: 5px;
    font-size: 28px;
  }
  .relocation .content-box {
    padding: 30px 10px 30px;
  }
  .content-box-about-wrap {
    flex-direction: column;
    align-items: normal;
    justify-content: normal;
  }
  .content-box-about-text-1 .text-accent {
    font-size: 36px;
  }
  .content-box-about-text-1 {
    font-size: 11px;
  }
  .content-box-about-text-3 {
    font-size: 24px;
    letter-spacing: normal;
  }
  .about .first-title_small {
    font-size: 34px;
  }
  .content-box_no-margin>p {
    font-size: 25px;
    letter-spacing: 3px;
  }
  .phone-menu__list{
    left: 50%;
  }
}

@media screen and (max-height: 400px) {
  .header{
    height: 400px;
  }
  .main__title{
    height: 246px;
  }
  .down-link{
    display: none;
  }
}