.header {
  background: url("../img/header_bg.jpeg") no-repeat center center / cover;
}

.hat {
  background: rgba(#000, 0.2);
  height: 78px;

  .container,
  .row {
    height: 100%;
  }
}

.logo {
  background: url("../img/logo.png") no-repeat center left $accent;
  background-position-x: 5px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  padding-left: 47px;
}

.compass {
  font-size: 18px;
  letter-spacing: 3px;
  color: #000000;
}

.europe {
  position: absolute;
  right: 22px;
  top: 57%;
  font-size: 10px;
  color: #9cbe8f;
  font-weight: 900;
}

.menu {
  height: 100%;

  &__list {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__link {
    font-weight: 900;
    color: #ffffff;
    font-size: 14px;
    transition: .2s ease;
    position: relative;

    &::after,
    &::before {
      content: '';
      display: block;
      background: $accent;
      height: 2px;
      width: 0;
      position: absolute;
      left: 50%;
      bottom: -3px;
      transition: .25s ease;
    }

    &::before {
      right: 50%;
      bottom: -3px;
      left: auto;
    }

    &:hover {
      color: #ffffff;
      font-size: 17px;

      &::after {
        width: 50%;
      }

      &::before {
        width: 50%;
      }
    }
  }
}

.phone {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;

  i {
    color: $accent;
    display: flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 2px solid $accent;
    transform: scale(-1, 1);
  }

  &s__wrap {
    margin-left: 20px;
  }
}

.phone__tel {
  font-weight: 900;
  font-size: 16px;
}

.main__title {
  height: calc(100vh - 78px - 143px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.first-title {
  color: #ffffff;
  letter-spacing: 50px;
  text-shadow: 0 0 46px rgba(0, 0, 0, .3);
}

.main__p {
  font-size: 51px;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 20px;
}

.down-link {
  color: $accent;
  position: relative;
  bottom: 0;
  left: 50%;
  display: flex;
  width: 36px;
  height: 36px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 2px solid $accent;
  transition: .2s ease;
  margin-left: -18px;


  &:hover {
    background: rgba(#fff, 0.3);
    color: $accent;
    box-shadow: 0 0 10px #fff;
  }
}

.second-title {
  text-align: center;
}

.advantages {

  &__wrap {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__title {
    margin-bottom: 50px;
  }

  @for $i from 1 through 3 {
    &__item_#{$i} {
      background: url("../img/advantage_#{$i}.png") no-repeat center top #f4f4f4;
    }
  }

  &__item {
    grid-column: span 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-position-y: 50px;
    padding: 145px 20px 53px 20px;
    color: #000000;
    transition: .2s ease;
    border: 1px solid #dddddd;

    &:hover {
      transform: scale(1.1);
      border-color: rgba($accent, .5);
      box-shadow: 0 0 10px rgba($accent, 1);
    }
  }

  &__text {
    text-align: center;
    margin-top: 13px;
  }
}

.moving {
  background: url("../img/relocztion_bg.jpeg") no-repeat center center / cover;
  height: 100vh;
  color: #fff;

  .container {
    height: 100%;
  }

  .row {
    height: 33.3%;
    display: flex;
    align-items: center;
  }

  h2, h3 {
    color: #ffffff;
  }

  h2 {
    margin-bottom: 20px;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(darken($accent, 35%), .86);
  }
}

.quote {
  width: 56%;
  margin: 0 auto;

  &__text {
    text-align: center;
    font-style: italic;
  }

  &__author {
    text-align: center;
    font-weight: 900;
    text-transform: uppercase;
  }
}

.question {
  border: 1px dashed #fff;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: calc(100vh / 20) 10px;

  .third-title {
    font-size: 22px;
  }

  &::before,
  &::after {
    content: "\f0d7";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 30px;
    height: 30px;
    border: 1px solid #fff;
    border-radius: 50%;
    background: darken($accent, 35%);
  }

  &::before {
    top: -15px;
  }

  &::after {
    bottom: -15px;
  }
}

.its-time {
  text-transform: uppercase;
  font-size: 19px;
  font-weight: 100;
  margin-bottom: 5px;
}

.answer {
  display: flex;
  flex-direction: column;
  align-items: center;

  .second-title {
    margin-bottom: 0;
    font-size: 37px;
  }

  &__text {
    text-align: center;
    font-weight: 100;
    margin-bottom: 50px;
  }
}

.learn-more {
  text-transform: uppercase;
  font-weight: 900;
  border: 2px solid #fff;
  border-radius: 6px;
  padding: 2% 3%;
  color: #fff;
  transition: .2s ease;

  &:hover {
    color: $accent;
    border-color: $accent;
    background: rgba(darken($accent, 40%), .5);
    box-shadow: 0 0 40px rgba(darken($accent, 40%), 1);
    transform: scale(1.1);
  }
}

.property {
  background: url("../img/property_bg.png") no-repeat center bottom / cover;

  &__ask {
    margin-top: 140px;
    position: relative;
    text-align: center;

    .learn-more {
      display: inline-block;
      margin-top: 40px;
      color: $accent;
      border-color: $accent;

      &:hover {
        background: #fff;
        box-shadow: 0 0 20px $accent;
      }
    }
  }

  &__text-wrap {
    background: #f4f4f4;
    border: 1px solid #ddd;
    padding: 56px 45px 36px 45px;
    color: #000;
    text-align: center;
    position: relative;
    z-index: 0;
  }

  &__text-wrap:nth-of-type(1) {
    margin-bottom: 8px;
  }

  &__text-wrap:nth-of-type(2)::before {
    content: "\f1cd";
    font-family: "Font Awesome 5 Free";
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    font-size: 30px;
    border-radius: 50%;
    color: #ffffff;
    background: $accent;
    position: absolute;
    left: 50%;
    z-index: 4;
    margin-left: -30px;
    top: -34px;
  }

  &__text_bold {
    font-weight: 900;
    font-size: 20px;
    text-transform: uppercase;
  }

  &__text {
    font-size: 17px;
  }
}

.house-img {
  position: absolute;
  top: 0;

  &-1 {
    left: 40%;
    margin: -67px;
  }

  &-2 {
    left: 50%;
    margin: -94px;
    top: -30px;
    z-index: 2;
  }

  &-3 {
    right: 40%;
    margin: -60px;
  }
}

.feedback {
  color: #000000;

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    text-transform: uppercase;
  }

  &__item {
    padding: 33px 40px 33px 111px;
    position: relative;
  }

  &__img-wrap {
    width: 132px;
    height: 132px;
    margin-top: -66px;
    margin-left: -66px;
    position: absolute;
    left: 0;
    top: 50%;
  }

  &__name {
    font-size: 16px;
    font-weight: 900;
  }

  &__link {
    font-size: 12px;
    font-weight: 900;
    color: $accent;
    margin-left: 40px;
    margin-right: auto;
    text-decoration: underline;
    transition: .2s ease;

    &:hover {
      transform: scale(1.2);
      text-decoration: underline;
      color: darken($accent, 20%);

    }
  }

  &__location {
    font-size: 12px;
    font-weight: 100;
    color: $accent;
  }

  &__text {
    text-align: justify;
    font-style: italic;
  }
}

.owl-item {
  padding-left: 70px;
}

.owl-carousel {
  position: relative;
}

.owl-prev {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -15px;
  margin-left: -15px;
  font-size: 20px;
  color: $accent;
  position: absolute;
  left: -50px;
  top: 50%;
  transition: .2s ease;
  border-radius: 50%;

  &:hover {
    background: rgba($accent, .2);
    cursor: pointer;
    transform: scale(1.3);
    color: darken($accent, 20%);
    border: 1px solid darken($accent, 20%);
  }
}

.owl-next {
  @extend .owl-prev;
  left: auto;
  right: -50px;
}

.owl-dots {
  display: flex;
  justify-content: space-between;
  width: 83px;
  margin: 0 auto;
}

.owl-dot {
  outline: none !important;
}

.owl-dot > span {
  display: block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #fff;
  border: 1px dashed $accent;
  transition: .2s ease;
}

.owl-dot.active > span {
  background: $accent;
}

.questions {
  background: $accent url("../img/consultation_bg.png") left top;

  &__title {
    position: relative;
    padding-bottom: 84px;

    h2 {
      color: #ffffff;
      margin-bottom: 15px;
    }

    &::after {
      content: "\f0dd";
      font-weight: 900;
      font-family: "Font Awesome 5 Free";
      display: flex;
      justify-content: center;
      width: 30px;
      height: 30px;
      color: #ffffff;
      border-radius: 50%;
      border: 1px solid #fff;
      margin-bottom: -15px;
      margin-left: -15px;
      left: 50%;
      bottom: 48px;
      position: absolute;
    }

    &-text {
      text-align: center;
      text-transform: uppercase;
      font-weight: 100;
      letter-spacing: 2px;
      font-size: 21px;
    }
  }
}

.feedback-form {
  display: grid;
  grid-gap: 10px;

  &__input {
    width: 100%;
    padding: 23px 29px;
    font-family: Arial, sans-serif;
    border: 1px solid #fff;
    border-radius: 3px;
    background: #32c2fd;
    color: #ffffff;
    outline: none;
    transition: .2s ease;

    &:focus {
      transform: scale(1.1);
      box-shadow: 0 0 35px 0 rgba(#0D0D0D, .6);

      &::placeholder {
        font-size: 10px;
        color: #ffffff;
      }
    }

    &::placeholder {
      transition: .2s ease;
      text-transform: uppercase;
      color: rgba(#fff, .68);
    }
  }
}

.submit-btn {
  text-transform: uppercase;
  text-align: center;
  font-weight: 900;
  font-size: 15px;
  background: transparent;
  border: 2px solid #fff;
  margin-top: 15px;
  transition: .2s ease;

  &:hover {
    transform: scale(1.1);
    background: rgba(#fff, .1);
    color: #ffffff;
    box-shadow: 0 0 10px 0 #000000;
  }
}

.footer {
  padding: 21px 0;
  background: $accent;
  position: relative;

  &::before {
    content: "";
    background: linear-gradient(to top, rgba(#000, .3) 0%, transparent 100%);
    height: 100px;
    width: 100%;
    position: absolute;
    top: -100px;
    left: 0;
  }

  &__text-wrap {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    font-size: 14px;
    font-weight: 100;
    color: #dbdbdb
  }

  .phone i {
    border-color: #ffffff;
    color: #ffffff;
  }
}

.policy {
  color: #dbdbdb;
  background-image: linear-gradient(to right, #dbdbdb 50%, transparent 50%);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 2px;
  transition: .2s ease;

  &:hover {
    color: #ffffff;
    font-size: 15px;
    background-image: linear-gradient(to right, #fff 50%, transparent 50%);
  }
}

