$bar-width: 50px;
$bar-height: 4px;
$bar-spacing: 15px;

.menu-wrapper {
  width: $bar-width;
  height: $bar-height + $bar-spacing*2;
  cursor: pointer;
  position: fixed;
  top: 22px;
  right: 30px;
  z-index: 5;
  display: none;
}

.hamburger-menu,
.hamburger-menu:after,
.hamburger-menu:before {
  width: $bar-width;
  height: $bar-height;
}

.hamburger-menu {
  transform: translateY($bar-spacing);
  background: lighten($accent,20%);
  transition: all 0ms 300ms;

  &.animate {
    background: rgba(255, 255, 255, 0);
  }
}

.hamburger-menu:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: $bar-spacing;
  background: lighten($accent,20%);
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.hamburger-menu:after {
  content: "";
  position: absolute;
  left: 0;
  top: $bar-spacing;
  background: lighten($accent,20%);
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.hamburger-menu.animate:after {
  background: $accent;
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);;
}

.hamburger-menu.animate:before {
  background: $accent;
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);;
}

.phone-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: darken(rgba($accent,1),35%) url("../img/consultation_bg.png") repeat center center;
  z-index: 4;
  opacity: 0;
  transition: .6s ease;
  pointer-events: none;
  cursor: pointer;

  &-active {
    opacity: 1;
    pointer-events: auto;
  }

  &__list{
    display: grid;
    grid-gap: 20px;
    width: 280px;
    position: absolute;
    margin-left: -140px;
    left: 30%;
    margin-top: -217px;
    top: 50%;
  }

  &__link {
    border: 2px solid lighten($accent,20%);
    background: rgba(#000,.2);
    display: block;
    text-align: center;
    padding: 20px 0;
    text-transform: uppercase;
    font-weight: 900;
    font-size: 18px;
    color: lighten($accent,20%);
    transition: .2s ease;

    &:hover,
    &-active{
      color: #fff;
      transform: scale(1.1);
      box-shadow: $accent 0 0 40px 0;
    }
  }
}

.no-scroll{
